import type { TemplateRef } from '@angular/core';
import { Component, ContentChild, Input, OnChanges, OnInit } from '@angular/core';
import { StepInstance } from '../../shared/model/workflows/step-instance';
import { WorkflowActor } from '../../shared/model/workflows/workflow-actor';
import { WorkflowModel } from '../../shared/model/workflows/workflow-model';
import { StepModel } from '../../shared/model/workflows/step-model';
import { WorkflowInstance } from '../../shared/model/workflows/workflow-instance';
import { StepState } from '../../shared/model/workflows/step-state';

@Component({
  selector: 'ls-workflow-model-viewer',
  templateUrl: './ls-workflow-model-viewer.component.html',
  styleUrls: ['./ls-workflow-model-viewer.component.scss']
})
export class LsWorkflowModelViewerComponent implements OnInit, OnChanges {


  @ContentChild('passedStepContent') passedStepTemplateRef: TemplateRef<any>;
  @ContentChild('pendingStepContent') pendingStepTemplateRef: TemplateRef<any>;

  @Input() circuit: WorkflowModel | WorkflowInstance;
  @Input() activeStep: number;
  @Input() messages: any;

  @Input() getStepIconClassFct: (step: StepModel) => string;
  @Input() getStepTooltipFct: (step: StepModel) => string;
  @Input() getCircuitEndAdditionalClassesFct: () => string | [];

  @Input() isInstance = false;
  isOver = false;
  isRejected = false;

  // messages = CircuitDisplayMessages;

  constructor() {
  }

  ngOnInit() {
    if (!this.circuit) {
      console.error('Missing input circuit data');
      this.circuit = new WorkflowModel();
    }

    if (!this.messages) {
      throw new Error('Missing localised messages for LsWorkflowViewerComponent!');
    }

    if (!this.getStepIconClassFct) {
      throw new Error('Missing getStepIconClassFct for LsWorkflowViewerComponent!');
    }

    if (!this.getStepTooltipFct) {
      throw new Error('Missing getStepTooltipFct for LsWorkflowViewerComponent!');
    }
  }

  ngOnChanges() {
    console.debug('LsWorkflowViewerComponent::ngOnChanges, ', this);
    if (this.isInstance) {
      console.debug('LsWorkflowViewerComponent::ngOnChanges - circuit is an instance ');
      const workflow = this.circuit as WorkflowInstance;
      this.activeStep = workflow.currentStepIdx;
      this.isOver = workflow.isOver;
      this.isRejected = workflow.rejected;
    }
  }

  getStepIconClass(step: StepModel): string {
    return this.getStepIconClassFct(step);
  }

  getStepTooltip(step: StepModel): string {
    return this.getStepTooltipFct(step);
  }

  isActiveStep(stepIdx: number): boolean {
    return !this.isRejected && !this.isOver && stepIdx === this.activeStep;
  }

  isPassed(step: StepInstance) {
    return this.isInstance && ((step.state === StepState.VALIDATED) || (step.state === StepState.REJECTED));
  }

  isActualValidator(step: StepInstance, validator: WorkflowActor) {
    return this.isInstance && (step.validatedBy.some((elem) => elem.id === validator.id));
  }

  isCircuitOver() {
    return this.isOver && !this.isRejected;
  }

  getCircuitEndAdditionalClasses() {

    if (this.getCircuitEndAdditionalClassesFct) {
      return this.getCircuitEndAdditionalClassesFct();
    }

    return '';
  }

  getValidatorClasses(step: StepInstance, validator: WorkflowActor): string {
    if (this.isActualValidator(step, validator)) {
      if (step.state === StepState.REJECTED) {
        return this.messages.REJECTED_BY_ICON_CLASS;
      } else {
        return this.messages.VALIDATED_BY_ICON_CLASS;
      }
    }
    return '';
  }

}
