import type { QueryList, TemplateRef } from '@angular/core';
import { Component, ContentChild, EventEmitter, Input, OnInit, Output, ViewChild, ViewChildren } from '@angular/core';
import { NgbPopover } from '@ng-bootstrap/ng-bootstrap';
import { WorkflowModel } from '../../shared/model/workflows/workflow-model';
import { StepModel } from '../../shared/model/workflows/step-model';
import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { WorkflowModelLocalizedMessages, WorkflowModelOptions } from '../../shared/model/workflows/workflow-model-options';

@Component({
  selector: 'ls-workflow-editor',
  templateUrl: './ls-workflow-editor.component.html',
  styleUrls: ['./ls-workflow-editor.component.scss']
})
export class LsWorkflowEditorComponent implements OnInit {

  currentEditedStepIdx: number;

  @ViewChild('endpointPopoverHandle', {read: NgbPopover}) endpointPopoverElementRef: NgbPopover;
  @ViewChildren(NgbPopover) allCircuitEditionPopover: QueryList<NgbPopover>;
  currentPopover: NgbPopover;


  @ContentChild('popEditContent') popEditContentTemplateRef: TemplateRef<any>;
  @ContentChild('popCreateContent') popCreateContentTemplateRef: TemplateRef<any>;
  @ContentChild('endpointPopoverEditContent') endpointPopoverEditContentRef: TemplateRef<any>;
  @ContentChild('stepDisplayContent') stepDisplayContentRef: TemplateRef<any>;
  @ContentChild('lastStepDisplayContent') lastStepDisplayContentRef: TemplateRef<any>;


  messages: WorkflowModelLocalizedMessages = {
    CREATE_STEP_TITLE: 'Ajouter une Ã©tape',
    EDIT_STEP_TITLE: 'Modification de l\'Ã©tape',
    ADD_STEP_BTN_TXT: 'Ajouter une Ã©tape',
    FIRST_STEP_TXT: 'DÃ©but',
    LAST_STEP_TXT: 'Fin',
  };
  activeOptions: WorkflowModelOptions = {
    startIconClass: 'far fa-flag',
    endIconClass: 'fas fa-flag-checkered',
    editorGlobalLayoutClass: 'ls-workflow-editor-default-layout',
    additionalFinalEndpointClasses: ''
  };
  @Input() endpointIsEditable = false;
  @Input() getStepIconClassFct: (step: StepModel) => string;
  @Input() getStepTooltipFct: (step: StepModel) => string;
  @Input() confirmStepDeletionFct: (step: StepModel) => Promise<boolean>;

  @Input() lastStepData: StepModel;

  workflowData: WorkflowModel;
  @Output() workflowChange = new EventEmitter<WorkflowModel>();

  constructor() {
  }

  @Input() get localizedMessages() {
    return this.messages;
  }

  set localizedMessages(value: WorkflowModelLocalizedMessages) {
    this.messages = Object.assign(this.messages, value);
  }

  @Input() get options() {
    return this.activeOptions;
  }

  set options(value: WorkflowModelOptions) {
    this.activeOptions = Object.assign(this.activeOptions, value);
  }

  @Input() get workflow(): WorkflowModel {
    return this.workflowData;
  }

  set workflow(newValue: WorkflowModel) {
    this.workflowData = newValue;
    this.workflowChange.emit(newValue);
  }

  ngOnInit() {
    if (!this.workflowData) {
      console.error('Missing input workflow data');
      this.workflowData = new WorkflowModel();
    }

    if (!this.messages) {
      throw new Error('Missing localised messages for LsWorkflowViewerComponent!');
    }

  }

  workflowDataChanged() {
    this.workflowChange.emit(this.workflowData);
  }

  askForStepDeletion(stepIdx: number) {
    console.debug('delete step');

    if (this.confirmStepDeletionFct) {
      this.confirmStepDeletionFct(this.workflowData.steps[stepIdx]).then(
        result => {
          if (result) {
            this.deleteStep(stepIdx);
          }
        }
      );
    } else {
      this.deleteStep(stepIdx);
    }
  }


  confirmStepCreation(stepData: StepModel, idx: number) {
    this.addStep(stepData, idx);
    this.closeCurrentPopover();
  }

  confirmStepEdition(stepData: StepModel, idx: number) {
    this.replaceStep(stepData, idx);
    this.closeCurrentPopover();
  }

  confirmFinalStepEdition() {
    this.closeCurrentPopover();
  }

  cancelStepEdition() {
    this.closeCurrentPopover();
  }

  cancelStepCreation(idx: number) {
    this.closeCurrentPopover();
  }

  isFirstStep(): boolean {
    return (!this.workflowData || this.workflowData.steps.length === 0);
  }

  getAdditionalStepClass(idx: number): string {
    if (this.currentEditedStepIdx !== null && this.currentEditedStepIdx === idx) {
      return 'selected';
    }
    return '';
  }

  getStepIconClass(step: StepModel): string {
    if (this.getStepIconClassFct) {
      return this.getStepIconClassFct(step);
    }
    return '';
  }

  getStepTooltip(step: StepModel): string {
    if (this.getStepTooltipFct) {
      return this.getStepTooltipFct(step);
    }
    return '';
  }

  public cloneStep(src: StepModel): StepModel {
    return src.clone();
  }

  public createStep(stepIdx: number) {
    const newStep = new StepModel();

    this.currentPopover = this.getCreationPopoverForIdx(stepIdx);
    this.currentPopover.hidden.subscribe(() => this.cancelStepCreation(stepIdx));
    this.currentPopover.open(
      {
        stepData: newStep,
        idx: stepIdx,
        cancelStepCreation: this.cancelStepCreation.bind(this),
        confirmStepCreation: this.confirmStepCreation.bind(this)
      });
  }

  public editStep(stepIdx: number) {
    this.currentEditedStepIdx = stepIdx;
    const editingStep = this.cloneStep(this.workflowData.steps[stepIdx]);

    this.currentPopover = this.getEditionPopoverForIdx(stepIdx);
    this.currentPopover.hidden.subscribe(() => this.cancelStepEdition());
    this.currentPopover.open({
      stepData: editingStep,
      idx: stepIdx,
      cancelStepEdition: this.cancelStepEdition.bind(this),
      confirmStepEdition: this.confirmStepEdition.bind(this)
    });
  }

  public editFinalStep() {
    const editingStep = this.cloneStep(this.lastStepData);
    this.currentPopover = this.endpointPopoverElementRef;
    this.currentPopover.hidden.subscribe(() => this.cancelStepEdition());
    this.currentPopover.open({
      stepData: editingStep,
      cancelStepEdition: this.cancelStepEdition.bind(this),
      confirmStepEdition: this.confirmFinalStepEdition.bind(this)
    });
  }

  drop(event: CdkDragDrop<StepModel[]>) {
    moveItemInArray(this.workflowData.steps, event.previousIndex, event.currentIndex);
    this.workflowDataChanged();
  }

  dragDisabledForIdx(idx: number) {
    return (this.currentEditedStepIdx !== null && this.currentEditedStepIdx === idx);
  }

  private closeCurrentPopover() {
    if (this.currentPopover && this.currentPopover.isOpen()) {
      this.currentPopover.close();
    }
    this.currentPopover = null;
    this.currentEditedStepIdx = null;
  }

  private addStep(step: StepModel, pos: number): void {
    this.workflowData.steps.splice(pos, 0, step);
    this.workflowDataChanged();
  }

  private replaceStep(newStep: StepModel, stepIdx: number): void {
    this.workflowData.steps.splice(stepIdx, 1, newStep);
    this.workflowDataChanged();
  }

  private deleteStep(stepIdx: number) {
    this.workflowData.steps.splice(stepIdx, 1);
    this.workflowDataChanged();
  }

  private getCreationPopoverForIdx(idx: number): NgbPopover {
    const filteredList = this.allCircuitEditionPopover.filter(
      (item) => {
        const elem = (item as any)._elementRef;
        if (elem && elem.nativeElement) {
          return elem.nativeElement.hasAttribute('create-btn');
        }
        return true;
      });

    return filteredList[idx];
  }

  private getEditionPopoverForIdx(idx: number): NgbPopover {
    const filteredList = this.allCircuitEditionPopover.filter(
      (item) => {
        const elem = (item as any)._elementRef;
        if (elem && elem.nativeElement) {
          return !(elem.nativeElement.hasAttribute('create-btn'));
        }
        return true;
      });

    return filteredList[idx];
  }

}
